.footer {
  padding: 40px 0;

  background-color: $color-neon-blue;
  background-color: #191919;

  // border-top-left-radius: 20px;
  // border-top-right-radius: 20px;
  box-shadow: 1px -1px 17px 2px rgba(255,255,255,10%);

  color: white;

  &__box {
    display: flex;
    justify-content: space-between;
  }

  &__social-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 20px;
  }

  &__social-link {
    
    & svg {
      width: 24px;
      height: 24px;

      fill: white;
      stroke: white;
    }
  }
}
