.our-works {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #ffd500;
  background-image: url("../img/svg/point-pattern.svg");


  &__slider {
    margin-bottom: 50px;
  }

  &__slide {
    height: 100%;

    & img {
      // height: 100%;
      height: 500px;
      object-fit: cover;
      border-radius: 12px;
    }
  }
  
  & p {
    color: #191919;
  }
}