.advantages {
  position: relative;
  padding-top: 100px;
  padding-bottom: 150px;
  background-color: #191919;

  &__box {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  &__item {
    max-width: 300px;

    &-1,
    &-2,
    &-3,
    &-4 {
      grid-column: 1 / span 4;
      text-align: right;
    }

    &-2 {
      grid-row: 2;
    }
    
    &-3 {
      grid-row: 3;
    }
    
    &-4 {
      grid-row: 4;
    }

    &-5 {
      grid-row: 4;
      grid-column: 5 / span 4;
      justify-self: center;
    }

    &-8,
    &-9,
    &-10,
    &-11,
    &-12 {
      grid-row: 1;
      grid-column: 10 / span 3;
      justify-self: end;
    }

    &-9 {
      grid-row: 2;
    }
    
    &-10 {
      grid-row: 3;
    }
    
    &-11 {
      grid-row: 4;
    }

    &-12 {
      grid-row: 5;
    }
  }

  &__item-title {
    color: #ffd500;
    text-transform: uppercase;
  }

  &__item-text {
    color: white;
  }

  &__picture {
    height: 600px;
    grid-row: 1 / span 3;
    grid-column: 5 / span 4;
    justify-self: center;
    
    & img {
      height: 100%;
    }
  }




}