.header {
  position: sticky;
  top: 0;
  padding: 28px 0;
  z-index: 100;

  background-color: #191919;
  box-shadow: 1px -1px 12px 2px rgba(255,255,255,10%);

  .container {
    display: flex;
  }

  &__logo {
    color: $color-default-white;
  }

  &__callback {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  &__phone {
    color: $color-default-white;
    transition: color $trans-default;
    padding: 10px;

    &:hover,
    &:focus {
      color: #a1821a;
    }

    &:active {
      color: rgba(#a1821a, 70%);
    }
  }

  &__callback-btn {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    color: #a1821a;
    text-transform: uppercase;
    text-wrap: nowrap;
    border: 1px solid #a1821a;
    padding: 8px 15px;
    border-radius: 12px;
    background-color: transparent;
    cursor: pointer;

    & svg {
      width: 24px;
      height: 24px;
      fill: #ffd500;
      stroke: red;
    }
  }
}
