.faq {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #191919;
  background-image: url("../img/content/bg-faq.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  color: white;

  &--padding {
    padding: 40px 0;
  }

  &__wrapper {}

  &__title {}

  &__accordion-element {
    &.is-active {
      & .faq__accordion-btn {
        & svg {
          transform: rotate(90deg);
        }
      }

      & .faq__accordion-content--border {
        border-bottom-color: $color-link-water;
      }

      & .faq__accordion-btn--border {
        border-bottom-color: transparent;
      }
    }
  }

  &__accordion-btn {
    display: flex;
    align-items: center;
    color: $color-default-white;
    background-color: transparent;
    background-color: #191919;
    border: 1px solid transparent;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.2;
    transition: color $trans-default;

    &--border {
      border-bottom: 1px solid $color-link-water;
    }

    &:hover,
    &:focus {
      color: #ffd500;
    }

    &:active {
      color: rgba(#ffd500, 70%);
    }

    & svg {
      width: 24px;
      height: 24px;
      margin-right: 5px;
      transition: transform $trans-default;
      flex-shrink: 0;
    }

    @include vp-767 {
      text-align: left;
    }
  }

  &__accordion-content {
    color: #191919;
    background-color: white;
    border-radius: 12px;

    &--border {
      border: 1px solid transparent;
    }
  }
}