.callback {
  min-height: 100vh;
  background-color: #ffd500;
  padding-top: 100px;
  padding-bottom: 100px;

  &__form {
    & form {
      display: flex;
      flex-direction: column;
      gap: 20px;

      & input,
      & textarea {
        width: 100%;
      }

      & button {
        cursor: pointer;
      }
    }
  }
}