.car-covers {
  background-color: #191919;
  padding-top: 100px;
  padding-bottom: 100px;
  min-height: 100vh;

  color: white;

  &--color {
    background-color: #ffd500;
    color: #191919
  }

  &__box {
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    gap: 20px;
  }

  &__card {
    background-color: goldenrod;
    padding: 20px;
    width: 50%;
    border-radius: 20px;
    box-shadow: 1px 1px 1px 1px rgba(white, 50%);

      &--color {
        background-color: #191919;
        color: white;
      }
  }

  &__slide {
    
    & img {
      width: 100%;
      height: 300px;
      object-fit: cover;
      border-radius: 20px;
    }
  }

  &__text {
    text-align: justify;
  }



  &__important {
    
    text-align: right;
  }

   &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    counter-reset: car-covers-advantages;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 30px;
    
  }

  &__item {
    position: relative;
    font-size: 24px;
    line-height: 1.2;

    min-width: 200px;
    box-shadow: 5px 2px 5px 0px rgba(white, 50%);
    padding: 10px;
    padding-right: 30px;

    &::before {
      // position: absolute;
      // top: 0;
      // left: 0;
      counter-increment: car-covers-advantages;
      content: counter(car-covers-advantages) " ";
      color: rgba(white, 50%);
      font-size: 60px;
      line-height: 1.5;
    }
  }


}